

import { PDFLoader } from "./PDFLoader";
import {PDFTextLayer} from "./PDFTextLayer";

var av = Autodesk.Viewing;
var aveh = AutodeskNamespace('Autodesk.Viewing.Extensions.PDF');


/**
 * Registers a FileLoader to enhance `viewer.loadModel()` to allow loading of PDF files.
 * The viewer will render a single page at a time.
 *
 * The extension id is: `Autodesk.PDF`
 *
 * @example
 * // Create Viewer instance and load PDF file on page 1
 * Autodesk.Viewing.Initializer(options, function() {
 *     var viewer = new Autodesk.Viewing.Viewer3D(div,config3d);
 *     viewer.start();
 *     viewer.loadExtension('Autodesk.PDF').then(function() {
 *         // URL parameter `page` will override value passed to loadModel
 *         viewer.loadModel('path/to/file.pdf', { page: 1 });
 *     });
 * });
 *
 *
 * @memberof Autodesk.Viewing.Extensions
 * @alias Autodesk.Viewing.Extensions.PDFExtension
 * @see {@link Autodesk.Viewing.Extension} for common inherited methods.
 * @class
 */
class PDFExtension extends av.Extension {

    constructor(viewer, options) {
        super(viewer, options);
        this.onModelLoaded = this.onModelLoaded.bind(this);
        this.onPopState = this.onPopState.bind(this);
        // each time pdf change a page, tear down will happen, we need to use PDFLoader.useTextLayer to save the options
        this.showTextLayer = options ? options.useTextLayer : PDFLoader.useTextLayer;
        PDFLoader.useTextLayer = this.showTextLayer || false;
    }

    load() {
        av.FileLoaderManager.registerFileLoader("pdf", ["pdf"], PDFLoader);
        if(!av.isNodeJS()) {
            this.viewer.addEventListener(av.GEOMETRY_LOADED_EVENT, this.onModelLoaded);
            window.addEventListener("popstate", this.onPopState);

            this.textLayer = new PDFTextLayer(this.viewer, this.showTextLayer);
            this.textLayer.onModelLoaded();
        }
        return true;
    }

    unload() {
        av.FileLoaderManager.unregisterFileLoader("pdf");
        if(!av.isNodeJS()) {
            this.viewer.removeEventListener(av.GEOMETRY_LOADED_EVENT, this.onModelLoaded);
            window.removeEventListener("popstate", this.onPopState);

            this.textLayer.destroy();
            this.textLayer = null;
        }
        return true;
    }

    onModelLoaded(event) {
        if(event && event.model && event.model.getDocumentNode() && event.model.getData().isPdf) {
            var url = new URL(window.location.href);
            var bubbleNode = event.model.getDocumentNode();
            var pageIndex = bubbleNode.data.page;
            if(url.searchParams.get("page") != pageIndex) {
                url.searchParams.set("page", pageIndex);
                window.history.pushState(undefined, "Page:" + pageIndex, url.href);
            }
        }
    }

    onPopState(event) {
        var model = this.viewer.model;
        if(event && model && model.getDocumentNode() && model.getData().isPdf) {
            var url = new URL(window.location.href);
            var pageIndex = parseInt(url.searchParams.get("page")) || 1;
            var rootNode = model.getDocumentNode().getRootNode();

            var bubbleNode = rootNode.children[pageIndex - 1];
            this.viewer.dispatchEvent({ type: av.HYPERLINK_EVENT, data: { bubbleNode: bubbleNode } });
            this.viewer.loadDocumentNode(rootNode.getDocument(), bubbleNode);
        }
    }

    activate() { return true; }
    deactivate() { return false; }
}

aveh.PDFExtension = PDFExtension;
aveh.PDFLoader = PDFLoader;

av.theExtensionManager.registerExtension('Autodesk.PDF', PDFExtension);

export { PDFLoader, PDFExtension }